<script>
import {useStore} from 'vuex'
import {computed, ref, onMounted, onUnmounted} from 'vue';
import router from '@/router/index';
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min';
export default {
  name: "RightSectionCop",
  setup() {
    $("#myModal").prependTo("body");
    const store = useStore();
    let username = ref('');
    let password = ref('');
    let password_confirm = ref('');
    let error_message = ref('');
    let is_login_success = ref(false);
    let cur_week = ref(3);
    // const lv_today_clock_in_time = ref('Êú™ÊâìÂç°');
    // const lv_clock_in_rank = ref(0);
    const cur_uid = ref();
    const cur_status = ref(false);
    const func_login_click = () => {
      cur_status.value = false;
    };
    const func_register_click = () => {
      cur_status.value = true;
    };
    const func_close_modal = () => {
    };

    let cur_chervon_status = ref(false);
    const func_change_chevron_status = () =>{
        cur_chervon_status.value = !cur_chervon_status.value;
    }
    const func_save_uid = () =>{
      // Â¶ÇÊûú‰∏çÊòØÊï∞Â≠óÂ∞±ÊòæÁ§∫ÈîôËØØ
      if (isNaN(cur_uid.value)) {
        alert("ËØ∑ËæìÂÖ•uid");
        return;
      }
      const lv_user = store.state.user;
      lv_user.uid = cur_uid.value;
      let api_flg = true;
      store.dispatch('act_update_user', {
        user_info: lv_user,
        api_flg: api_flg,
      });
      // store.commit('updateUser', lv_user);
      alert('‰øùÂ≠òÊàêÂäü');
      $('#check_uid').click();
    };
    const login = () => {
      error_message.value = "";
      store.dispatch("login", {
        username: username.value,
        password: password.value,
        success() {
          $(".btn-close").click();
          router.push({name: 'home'});
          func_get_rank_list();
          func_show_info();
        },
        error() {
          error_message.value = "Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØ";
        }
      });
    }
    const register = () => {
      error_message.value = "";
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/register/",
        type: "POST",
        data: {
          username: username.value,
          password: password.value,
          password_confirm: password_confirm.value,
        },
        success(resp) {
          if (resp.result === "success") {
            store.dispatch("login", {
              username: username.value,
              password: password.value,
              success() {
                $(".btn-close").click();
                // Âà∑Êñ∞ÁïåÈù¢
                location.reload();
                router.push({name: 'home'});
              },
              error() {
                error_message.value = "Á≥ªÁªüÂºÇÂ∏∏ÔºåËØ∑Á®çÂêéÈáçËØï";
              }
            });
          } else {
            error_message.value = resp.result;
          }
        }
      });

    };

    const func_logout = () => {
      // Âà∑Êñ∞ÁïåÈù¢
      store.commit('logout');
      window.location.reload();
    };
    const lv_this_day_clock_in_time = ref("Êú™ÊâìÂç°");
    const lv_this_day_clock_rank = ref("Êó†");
    const lv_continue_clock_in_time = ref(0);
    const lv_max_continue_clock_in_time = ref(0);
    const lv_this_week_clock_in_time = ref(2);
    const lv_this_month_clock_in_time = ref(0);
    const lv_this_day_clock_in_count = ref(0);
    const lv_user_photo = ref();
    const lv_uname = ref('');
    let lv_rank_str = computed(() => `${lv_this_day_clock_rank.value}/${lv_this_day_clock_in_count.value}`);
    const func_get_diff_time = (start_time) => {
        let lv_start_time = new Date(start_time);
        let lv_cur_time = new Date();
        let lv_diff = lv_cur_time.getTime() - lv_start_time.getTime();
        let lv_diff_secs = Math.floor(lv_diff / 1000);
        return lv_diff_secs;
    };
    const lv_this_day_clock_in_rank_list = ref([]);
    const lv_cur_doing_task = ref({
      "task_name": "",
      "start_time": "",
      "task_length": 0,
      "job_id": 0
    });
    const lv_cur_pgs = ref(0);
    const timer = ref(null);
    const lv_pgsBarStyle = ref({});
    const updateProgress = () => {
      const taskLength = lv_cur_doing_task.value.task_length;
      let lv_diff_sec = func_get_diff_time(lv_cur_doing_task.value.start_time);
      let lv_remain_sec = lv_cur_doing_task.value.task_length - lv_diff_sec;
      let lv_diff_min = Math.floor(lv_remain_sec / 60);
      lv_cur_pgs.value = Math.floor(lv_diff_sec / (taskLength === 0 ? 1 : taskLength) * 100);
      lv_pgsBarStyle.value = {
        background: `radial-gradient(closest-side, #f7f7f7 79%, transparent 80%, transparent 100%),
                 conic-gradient(#CCC ${lv_cur_pgs.value}%, #006ED3 0%)`,
        '--remaining-time': `"${lv_diff_min}:${lv_remain_sec % 60}"`
      };
    };
    // const pgsBarStyle
    onMounted(() => {
      updateProgress(); // ÂàùÂßãÂåñÊó∂ÂÖàËÆ°ÁÆó‰∏ÄÊ¨°
      timer.value = setInterval(updateProgress, 1000); // ÊØèÁßíÊõ¥Êñ∞‰∏ÄÊ¨°
    });

    onUnmounted(() => {
      clearInterval(timer.value); // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÈô§ÂÆöÊó∂Âô®
    });
    const lv_task_length_str = computed(() => {
      let lv_task_length = lv_cur_doing_task.value.task_length;
      let lv_task_length_hours = Math.floor(lv_task_length / 3600);
      let lv_task_length_min = Math.floor((lv_task_length % 3600) / 60);
      let lv_task_length_sec = lv_task_length % 60;
      if (lv_task_length < 7200) {
        // Â∞è‰∫é 120 ÂàÜÈíü
        let lv_task_length_min = Math.floor(lv_task_length / 60);
        if (lv_task_length_sec === 0) {
          return `${lv_task_length_min}ÂàÜÈíü`;
        } else {
          return `${lv_task_length_min}ÂàÜÈíü${lv_task_length_sec}Áßí`;
        }
      } else {
        // Â§ß‰∫éÁ≠â‰∫é 120 ÂàÜÈíü
        if (lv_task_length_sec === 0) {
          return `${lv_task_length_hours}Â∞èÊó∂${lv_task_length_min}ÂàÜÈíü`;
        } else {
          return `${lv_task_length_hours}Â∞èÊó∂${lv_task_length_min}ÂàÜÈíü${lv_task_length_sec}Áßí`;
        }
      }
    });

    const func_show_info = () => {
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/getjson/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          'uid': store.state.user.uid,
        },
        success(resp) {
          if (resp.result === 'success') {
            let study_study_info = resp['json']['study_time_info'];
            let task_info = resp['json']['study_tasks_info'];
            let user_info = resp['json']['user_info'];
            let lv_last_clock_in_time = study_study_info['last_clock_in_time'];
            lv_this_week_clock_in_time.value = study_study_info['this_week_clock_in_time'];
            lv_this_month_clock_in_time.value = study_study_info['this_month_clock_in_time'];
            lv_continue_clock_in_time.value = study_study_info['cur_continue_clock_in_time'];
            lv_max_continue_clock_in_time.value = study_study_info['max_continue_clock_in_time'];
            lv_cur_doing_task.value = task_info['cur_doing_task'];
            lv_user_photo.value = user_info['photo'];
            lv_uname.value = user_info['uname'];
            const lv_user = store.state.user;
            let api_flg = true;
            lv_user.photo = lv_user_photo.value;
            lv_user.uname = lv_uname.value;
            store.dispatch('act_update_user', {
              user_info: lv_user,
              api_flg: api_flg,
            });
            console.log(lv_cur_doing_task.value);
            // Â¶ÇÊûú‰∏çÁ≠â‰∫é‰ªäÂ§©
            const func_is_today = (date) => {
              const today = new Date();
              return date.toDateString() === today.toDateString();
            };
            if(!func_is_today(new Date(lv_last_clock_in_time)))
              return;
            lv_this_day_clock_in_time.value = study_study_info['this_day_clock_in_time'];
            lv_this_day_clock_rank.value = study_study_info['this_day_clock_in_rank'];
          }
        },
        error(xhr, status){
          try{
            window.clearInterval(store.state.user.intervalFunc);
            let user = JSON.parse(localStorage.getItem("user"));
            // Âè™ÊúâÁôªÈôÜ‰∫ÜÊâçÈúÄË¶ÅÊõ¥Êñ∞token
            if(user !== null) store.dispatch("refresh_access_update", user.refresh);
          }
          catch{
            store.commit("judgeError", status);
          }
        },

      });
    };

    const func_not_bind_uid = () => {
      lv_this_day_clock_in_time.value = "Êú™ÁªëÂÆöuid";
    };
    if(cur_uid.value !== "")
        func_show_info();
    else
        func_not_bind_uid();
    const getTodayDate = () => {
      const today = new Date();
      const year = today.getFullYear();
      const month = (today.getMonth() + 1).toString().padStart(2, '0'); // Âä† 1 ÊòØÂõ†‰∏∫Êúà‰ªΩ‰ªé 0 ÂºÄÂßãËÆ°Êï∞ÔºåpadStart Ë°•Èõ∂
      const day = today.getDate().toString().padStart(2, '0');
      let lv_today_str = `${year}${month}${day}`;
      let lv_today_num = parseInt(lv_today_str);
      return lv_today_num;
    }
    const func_get_rank_list = () => {
      // Ëé∑Âèñ‰ªäÊó•ÁöÑÊó•Êúü Â¶Ç 20240604 ÁöÑÊ†ºÂºè
      let lv_cur_day = getTodayDate();
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/allinfo/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          'info_id': lv_cur_day,
        },
        success(resp) {
          if(resp.result=='success')
          {
            const lv_all_info = JSON.parse(resp['info']);
            lv_this_day_clock_in_count.value = lv_all_info['clock_in_count']; // ÂàùÂßãÂåñ‰ªäÊó•ÊéíÂêç
            lv_this_day_clock_in_rank_list.value = lv_all_info['clock_in_rank_list'];
            console.log(lv_this_day_clock_in_rank_list.value[0].photo);
          }

        },


      });
    };
    func_get_rank_list();
    return {
      username,
      password,
      password_confirm,
      error_message,
      login,
      store,
      func_login_click,
      func_register_click,
      cur_status,
      func_close_modal,
      is_login_success,
      register,
      func_change_chevron_status,
      cur_chervon_status,
      func_logout,
      cur_week,
      cur_uid,
      func_save_uid,
      lv_this_day_clock_in_time,
      lv_continue_clock_in_time,
      lv_max_continue_clock_in_time,
      lv_this_week_clock_in_time,
      lv_this_month_clock_in_time,
      lv_rank_str,
      lv_this_day_clock_in_rank_list,
      lv_cur_doing_task,
      lv_cur_pgs,
      lv_pgsBarStyle,
      lv_task_length_str,
    }


  }
}

</script>

